"use client"

import type React from "react"

import { Button } from "@/components/ui/button"
import { Sheet, SheetContent, SheetHeader, SheetTitle, SheetTrigger } from "@/components/ui/sheet"
import { Minus, Plus, Trash2, ShoppingBag } from "lucide-react"
import { usePanierStore } from "@/lib/store"
import Image from "next/image"
import Link from "next/link"
import { useState } from "react"

interface PanierSheetProps {
  children: React.ReactNode
}

export function PanierSheet({ children }: PanierSheetProps) {
  const [ouvert, setOuvert] = useState(false)
  const articles = usePanierStore((state) => state.articles)
  const totalPrix = usePanierStore((state) => state.totalPrix())
  const modifierQuantite = usePanierStore((state) => state.modifierQuantite)
  const retirerDuPanier = usePanierStore((state) => state.retirerDuPanier)

  return (
    <Sheet open={ouvert} onOpenChange={setOuvert}>
      <SheetTrigger asChild>{children}</SheetTrigger>
      <SheetContent className="flex w-full flex-col sm:max-w-lg">
        <SheetHeader>
          <SheetTitle>Panier ({articles.length})</SheetTitle>
        </SheetHeader>

        {articles.length === 0 ? (
          <div className="flex flex-1 flex-col items-center justify-center">
            <ShoppingBag className="mb-4 h-16 w-16 text-muted-foreground" />
            <p className="mb-2 font-medium">Votre panier est vide</p>
            <p className="text-sm text-muted-foreground">Ajoutez des produits pour commencer</p>
          </div>
        ) : (
          <>
            <div className="flex-1 overflow-auto">
              <div className="space-y-4">
                {articles.map((article, index) => (
                  <div key={`${article.produit.id}-${article.couleur.nom}-${article.taille || 'default'}`} className="flex gap-4">
                    <div className="relative h-24 w-24 overflow-hidden rounded bg-secondary">
                      <Image
                        src={article.couleur.imageFront || "/placeholder.svg"}
                        alt={article.produit.nom}
                        fill
                        className="object-cover"
                      />
                    </div>

                    <div className="flex flex-1 flex-col justify-between">
                      <div>
                        <p className="font-medium">{article.produit.nom}</p>
                        <p className="text-sm text-muted-foreground">{article.couleur.nom}</p>
                        <p className="text-sm font-semibold">{(typeof article.prix === 'number' ? article.prix : article.produit.prix).toLocaleString()} DZD</p>
                      </div>

                      <div className="flex items-center justify-between">
                        <div className="flex items-center border border-border">
                          <Button
                            variant="ghost"
                            size="icon"
                            className="h-8 w-8 rounded-none"
                            onClick={() =>
                              modifierQuantite(article.produit.id, article.couleur.nom, article.quantite - 1, article.taille)
                            }
                          >
                            <Minus className="h-3 w-3" />
                          </Button>
                          <div className="flex h-8 w-10 items-center justify-center border-x border-border text-sm font-medium">
                            {article.quantite}
                          </div>
                          <Button
                            variant="ghost"
                            size="icon"
                            className="h-8 w-8 rounded-none"
                            onClick={() =>
                              modifierQuantite(article.produit.id, article.couleur.nom, article.quantite + 1, article.taille)
                            }
                          >
                            <Plus className="h-3 w-3" />
                          </Button>
                        </div>

                        <Button
                          variant="ghost"
                          size="icon"
                          className="h-8 w-8"
                          onClick={() => retirerDuPanier(article.produit.id, article.couleur.nom, article.taille)}
                        >
                          <Trash2 className="h-4 w-4" />
                        </Button>
                      </div>
                    </div>
                  </div>
                ))}
              </div>
            </div>

            <div className="space-y-4 border-t border-border pt-4">
              <div className="flex justify-between text-lg font-bold">
                <span>Total</span>
                <span>{totalPrix.toLocaleString()} DZD</span>
              </div>

              <Link href="/livraison" onClick={() => setOuvert(false)}>
                <Button className="w-full" size="lg">
                  Passer la commande
                </Button>
              </Link>
            </div>
          </>
        )}
      </SheetContent>
    </Sheet>
  )
}
